import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import CIcon from '@coreui/icons-react'
import { cilDataTransferDown } from '@coreui/icons'
import { CContainer, CRow, CCol, CButton, CFormInput } from '@coreui/react'
import { completeLists } from '../../mocks/quotationData'

const QuotationComplete = () => {
  const [dataList, setDataList] = useState(completeLists)

  const handleClick = (i) => {
    dataList[i].status = 'D'
    let newArray = dataList
    setDataList(newArray)
  }

  // init api all lists
  // search api show lists
  // click action change status C->D
  // if status === D btn disable

  return (
    <div className="px-3">
      <CContainer>
        <div className="form-box">
          <CRow>
            <CCol sm={3}>
              <CFormInput label="報價單編號" placeholder="請輸入報價單編號" />
            </CCol>
            <CCol sm={3}>
              <CFormInput label="客戶統編" placeholder="請輸入客戶統編" />
            </CCol>

            <CCol sm={2} className="d-flex align-items-end">
              <CButton color="primary">搜尋</CButton>
            </CCol>
          </CRow>
        </div>
        <div className="list-head">
          <CRow>
            <CCol className="text-align-center" sm={2}>
              報價單編號
            </CCol>
            <CCol className="text-align-center" sm={3}>
              客戶統編
            </CCol>
            <CCol className="text-align-center" sm={2}>
              建立時間
            </CCol>
            <CCol className="text-align-center" sm={2}>
              狀態
            </CCol>
            <CCol className="text-align-center" sm={2}>
              操作
            </CCol>
            <CCol className="text-align-center" sm={1}>
              下載
            </CCol>
          </CRow>
        </div>
        <div className="list-body">
          {dataList && dataList.length
            ? dataList.map((item, i) => {
                return (
                  <div className="list-item" key={item.num + i}>
                    <CRow className="align-items-center justify-content-center">
                      <CCol className="text-align-center" sm={2}>
                        {item.num}
                      </CCol>
                      <CCol className="text-align-center" sm={3}>
                        {item.client}
                      </CCol>
                      <CCol className="text-align-center" sm={2}>
                        {item.createDate}
                      </CCol>
                      <CCol className="text-align-center" sm={2}>
                        {item.statusText}
                      </CCol>
                      <CCol className="text-align-center" sm={2}>
                        <CButton
                          color="warning"
                          disabled={item.status !== 'C'}
                          onClick={() => handleClick(i)}
                        >
                          {item.status === 'C' ? '成案' : '已成案'}
                        </CButton>
                      </CCol>
                      <CCol className="text-align-center" sm={1}>
                        <Link to="/pdfContainer" className="action-icon">
                          <CIcon icon={cilDataTransferDown} />
                        </Link>
                      </CCol>
                    </CRow>
                  </div>
                )
              })
            : null}
        </div>
      </CContainer>
    </div>
  )
}

export default QuotationComplete
